@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.main {
  display: flex;

  @include notebook {
    flex-direction: column-reverse;
  }

  &__content {
    width: 100%;
    max-width: 800px;

    @include notebook {
      max-width: 100%;
    }
  }

  &__sidebar {
    width: 370px;
    margin-left: auto;

    @include notebook {
      margin-left: 0;
      width: 100%;
    }
  }

  &__apps {
    &:not(:last-child) {
      margin-bottom: 40px;

      @include notebook {
        margin-bottom: 30px;
      }
    }
  }
}

.heading {
  color: var(--text-primary);
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 15px;
}

.filters {
  &__heading {
    display: flex;
    align-items: center;
    font-size: 16px;
    margin-bottom: 10px;
  }

  &__heading-icon {
    margin-right: 5px;
  }

  &__input {
    margin-bottom: 15px;
    font-size: 16px;
    background-color: var(--tertiary-bg);
    border-radius: 3px;
    border: none;
    padding: 9px 10px;
    color: var(--text-primary);
    width: 250px;

    @include notebook {
      width: 100%;
    }

    &::placeholder {
      color: var(--text-primary);
    }
  }
}

.block {
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 15px;
  overflow: hidden;
  position: relative;

  @include notebook {
    padding: 10px;
  }

  &_yellow {
    background-image: var(--yellow-gradient);
    border: 1px solid var(--yellow-border);
  }

  &_purple {
    background-image: var(--purple-gradient);
    border: 1px solid var(--purple-border);
    max-height: 140px;
  }

  &_devices {
    min-height: 120px;
  }

  &__top,
  &__bottom {
    display: flex;
  }

  &__right {
    margin-left: 15px;
    min-width: 0;
    flex-grow: 1;
  }

  &__heading {
    font-size: 20px;
    color: var(--text-primary);
    font-weight: 600;
    margin-bottom: 10px;

    &-icon {
      margin: 0 4px;
      filter: var(--img-filter);
    }

    &_small {
      display: flex;
      font-size: 16px;
    }
  }

  &__item {
    line-height: 16px;

    &_stub {
      background-color: var(--stub-bg);
      border-radius: 6px;
      color: transparent;
      display: block;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__link {
    color: $primary;
  }

  &__more {
    display: flex;
    align-items: center;
    margin-top: 15px;
    margin-left: auto;
    color: var(--text-tertiary);
    font-weight: 600;
    font-size: 12px;
    white-space: nowrap;

    &-icon {
      height: 9px;
      margin-top: 1px;
      margin-left: 5px;
    }
  }

  &__text {
    color: var(--text-primary);
  }

  &__link-device {
    display: inline-block;
    margin-top: 20px;
    background: var(--bg);
    border: 1px solid lighten($primary, 35);
    border-radius: 2px;
    padding: 5px 10px;
    color: $primary;
    font-weight: 500;
  }

  &__device-desc {
    margin-bottom: 20px;
  }

  &__device-page {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--bg-transparent);
    box-shadow: 0 -1px 4px 0 rgba(203, 203, 203, 0.3);
    padding: 10px 15px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: var(--text-tertiary);
    font-weight: 600;
    font-size: 12px;
  }
}

.news {
  max-height: 350px;
  overflow: scroll;
  mask-image: linear-gradient(to bottom, transparent, #000 8% 92%, transparent);
  padding-top: 18px;
  margin-top: -15px;
  padding-bottom: 20px;

  @include notebook {
    max-height: 200px;
  }

  &__title,
  &__caption,
  &__date {
    display: block;
  }

  &__title {
    font-weight: 700;
  }

  &__caption {
    margin: 3px 0;
  }

  &__date {
    font-size: 12px;
    font-weight: 700;
    opacity: .4;
  }
}

.back-link {
  margin-bottom: 15px;
}
