@import "./src/assets/themes/appdb/variables.scss";

.icon {
    object-fit: cover;
    object-position: center;
    width: 80px;
    height: 80px;

    &_loading {
        opacity: 0;
    }

    &:not(&_vertical) {
        border-radius: 22.5%;
        mask: url(../../assets/ios-mask.svg) center/100% 100% no-repeat;
    }

    &_vertical {
        border-radius: 10px;
        height: 125px;
    }

    &:not(&_filled) {
        background-color: var(--stub-bg);
    }
}
